import './App.css';
import React, { useState } from 'react'
import { AddExpense } from './components/AddExpense';
import { Balance } from './components/Balance';
import { GlobalProvider } from './components/GlobalState';
import { Header } from './components/Header';
import { Output } from './components/Output';
import { Persons } from './components/Persons';
import head_img from './images/card-image.jpg';
import { Footer } from './components/Footer';
import { FinalHistory } from './components/FinalHistory';
import { TripName } from './components/TripName';
import { Expenses } from './components/Expenses';


function App() {
  const [screen, setScreen] = useState(0)
  function handleChange(e) {
    setScreen(e);
  }
  if (screen === 0) {
    return (
      <GlobalProvider>
        <div className="container-fluid cnt-img">
          <Header />
          <div className='cnt row justify-content-center py-5'>
            <div className='col-lg-4'>
              <div className='card'>
                <TripName handleChange={handleChange} />
              </div>
            </div>
          </div>

          <Footer />
        </div>
      </GlobalProvider>
    )
  }
  else if (screen === 1) {
    return (
      <GlobalProvider>
        <div className="container-fluid cnt-img">
          <Header />
          <div className='cnt row justify-content-center py-5'>
            <div className='col-lg-4'>
              <div className='card'>
                <Persons handleChange={handleChange}/>
              </div>
            </div>
          </div>

          <Footer />
        </div>
      </GlobalProvider>
    )
  }
  else if (screen === 2) {
    return (
      <GlobalProvider>
        <div className="container-fluid cnt-img">

          <Header />
          <div className='cnt row justify-content-center py-5'>
            <div className='col-lg-4'>
              <div className='card px-2'>
                <Expenses handleChange={handleChange} />
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </GlobalProvider>
    )
  }
  return (
    <GlobalProvider>
      <div className="container-fluid cnt-img">
        <Header />
        <div className="cnt row justify-content-center py-5">
          <div className='col-lg-6'>
            <div className='card px-2'>
              <Balance />
              <hr />
              <FinalHistory />
              <hr />
              <Output />
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </GlobalProvider>
  );
}

export default App;
