import '../App.css';
import React, { useState, useContext } from 'react'
import { GlobalContext } from './GlobalState';

export const TripName = ({ handleChange }) => {
    const [tripName, settripName] = useState("");
    const { addTripName } = useContext(GlobalContext)
    const onSubmit = e => {
        e.preventDefault();
        addTripName(tripName);
        if (tripName === "") {
            alert("Please enter name")
        }
        else {
            addTripName(tripName);
            handleChange(1);
        }
    }
    return (
        <div>
            <div className='card-title text-center py-4' >
                <h1>Welcome!</h1>
            </div>
            <div className='px-2'>
                <div className='text-center'>
                    <p>You can divide your expenses among your group using <b>Expense Divider</b></p>
                    <hr />
                    <h4>Instructions: -</h4>
                </div>
                <div className='text-left'>
                    <ul>
                        <li><b>Step 1: -</b> Enter your trip name.</li>
                        <li><b>Step 2: -</b> Add your trip members.</li>
                        <li><b>Step 3: -</b> Add expenses which includes the title, amount, paid by and paid for. Click calculate button.</li>
                        <li><b>Step 4: -</b> The last screen will display history of transactions, a table which displays expenses of individual person along with the settlement table.</li>
                    </ul>
                </div>
                <hr />
                <form>
                    <div>
                        <div className='row justify-content-center text-center'>
                            <label htmlFor="text"><b>Write your trip name:-</b></label>
                            <div className='col-7'>
                                <input type="text" placeholder="Trip name here..." value={tripName} onChange={(e) => settripName(e.target.value)} />
                                <button className="btn btn-next px-5 my-2" onClick={(e) => onSubmit(e)}>Start</button>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )
}
