import React,{useContext} from 'react'
import { GlobalContext } from './GlobalState'

export const Transaction = ({ trans }) => {
    const sign = trans.amount < 0 ? '-' : '+'
    const {deleteTransaction, persons} = useContext(GlobalContext)
    return (
        <li>
            <b> {trans.text} </b> <span>Rs. {Math.abs(trans.amount)}, paid by <b> {persons.filter(r => r.id==trans.paidBy)[0].name}</b></span>
            <button onClick={() => deleteTransaction(trans.id)} className="delete-btn">x</button>
        </li>
    )
}
