import '../App.css';
import React, { useState, useContext } from 'react'
import { GlobalContext } from './GlobalState'

export const AddExpense = () => {
    const [trans, setTrans] = useState({
        task: "",
        paidBy: "",
        amount: ""
    })
    const [paidTo, setpaidTo] = useState([])
    const { addTransaction, persons, transactions } = useContext(GlobalContext)
    console.log(persons)
    console.log(paidTo)
    function handleTrans(e) {
        const newTrans = { ...trans }
        newTrans[e.target.id] = e.target.value
        setTrans(newTrans)
    }

    function onSubmit(e) {
        e.preventDefault();
        if (trans.task != "" && trans.paidBy != "" && trans.amount != "" && paidTo.length !== 0) {
            const newTransaction = {
                id: Math.floor(Math.random() * 100000000),
                text: trans.task,
                amount: +trans.amount,
                amtDisplay: +trans.amount,
                paidBy: +trans.paidBy,
                paidTo
            }

            addTransaction(newTransaction);
            setTrans({
                task: "",
                paidBy: "",
                amount: ""
            })
            var y = document.getElementsByClassName("by");
            for (let i = 0; i < y.length; i++) {
                y[i].selected = false;
            }
            var x = document.getElementsByClassName("choose-person");
            for (let i = 0; i < x.length; i++) {
                x[i].checked = false;
            }
            setpaidTo([]);
        }
        else{
            alert("Fill all the fields");
        }
    }
    function choosePerson(e) {
        if (e.target.checked) {
            setpaidTo([...paidTo, +e.target.value])
            console.log(paidTo)
        }
        else {
            const index = paidTo.indexOf(+e.target.value)
            paidTo.splice(index, 1)
            console.log(paidTo)
        }
    }
    return (

        <div>
            <h4>Add new transaction</h4>
            <form onSubmit={onSubmit}>
                <div>
                    <label htmlFor="text"> <b> Paid for </b> </label>
                    <input type="text" id="task" placeholder="Enter text..." value={trans.task} onChange={(e) => handleTrans(e)} />
                </div>
                <div>
                    <label htmlFor="text"> <b> Paid by </b> </label>
                    <select className="form-select" aria-label="Default select example" id="paidBy" onChange={(e) => handleTrans(e)}>
                        <option selected>Choose Person</option>
                        {persons.map(person => (
                            <option className='by' key={person.id} value={person.id} >{person.name}</option>
                        ))}
                    </select>
                </div>

                <div>
                    <label htmlFor="amount"
                    > <b> Amount </b> <br />
                        (negative - expense, positive - income)</label
                    >
                    <input type="number" id="amount" placeholder="Enter amount..." value={trans.amount} onChange={(e) => handleTrans(e)} />
                </div>
                <div>
                    <p> <b> Choose person </b> </p>
                    {persons.map(person => (
                        <div key={person.id}>
                            <input className="form-check-input choose-person" name1="list" type="checkbox" onChange={(e) => choosePerson(e)} value={person.id} id="paidTo" />&nbsp;{person.name}<br />
                        </div>
                    ))}
                </div>
                <center>
                    <button className="btn btn-here">Add transaction</button>
                </center>
            </form>
        </div>
    )
}
