import React, { useContext } from 'react'
import { AddPerson } from './AddPerson'
import { GlobalContext } from './GlobalState'

export const Persons = ({handleChange}) => {
    const { persons, deletePerson, tripName } = useContext(GlobalContext);
    persons.reverse();
    function onSubmit(e) {
        e.preventDefault();
        if (persons.length !== 0) {
            handleChange(2);
        }
        else {
            alert("Add members")
        }
    }
    return (
        <div>
            <div className='card-title text-center py-4' >
                <h4>Hello! <b> {tripName}'s </b> squad</h4>
            </div>
            <div className='mx-4'>
                <h4>Persons list</h4>
                <ul id="list" className="list">
                    {persons.length == 0 ? "No person added..." : persons.map((person, ind) => (
                        <div key={person.id}>
                            <li id={person.id}>
                                {persons.length - ind}. &nbsp;
                                {person.name}
                                <button onClick={() => deletePerson(person.id)} className="delete-btn">x</button>
                            </li>
                        </div>
                    )).reverse()}
                </ul>
                <AddPerson />
            </div>
            <hr />
            <center>
                <button className="btn btn-next px-5 my-2" onClick={(e) => onSubmit(e)} >Save and next</button>
            </center>
        </div>
    )
}
