import React, { useContext } from 'react'
import { GlobalContext } from './GlobalState'

import { AddExpense } from './AddExpense';
import { Balance } from './Balance';
import { History } from './History';

export const Expenses = ({handleChange}) => {
    const {transactions} = useContext(GlobalContext)
    function onSubmit(e){
        e.preventDefault();
        if(transactions.length !== 0){
            handleChange(3);
        }
        else{
            alert("Add transactions")
        }
    }
    return (
        <>
            <Balance />
            <hr />
            <History />
            <hr />
            <AddExpense />
            <hr />
            <center>
                <button className="btn btn-next px-5" onClick={(e) => onSubmit(e)}>Calculate</button>
            </center>
        </>
    )
}
