import '../App.css';
import React, { useContext } from 'react'
import { GlobalContext } from './GlobalState';

export const Invoice = ({tripName, transactions, persons, personsDisplay, settlement1 }) => {
    let fsty = {
        fontSize: "5px",
        color: "#000000",
        margin: 0
    }
    let fsty1 = {
        fontSize: "10px",
        color: "#000000",
        margin: 0
    }

    let pad = {
        padding: "0 0.5rem 0.1rem 0.5rem",
        fontSize: "3px"       
    }

    return (
        <div style={{ width: "160px", margin: "20px 20px 0 20px" }}>
            <center><p style={fsty1}>{tripName}'s Invoice</p></center>
            <p style={fsty}>Transaction details: -</p>
            <table className='table table-success table-striped' >
                <thead>
                    <tr style={fsty}>
                        <th style={pad}>#</th>
                        <th style={pad}>Task</th>
                        <th style={pad}>Paid by</th>
                        <th style={pad}>Paid for</th>
                        <th style={pad}>Amount</th>
                    </tr>
                </thead>
                <tbody>
                    {transactions.map((trans, index) => (
                        <>
                            <tr key={index} style={fsty}>
                                <td style={pad}>{index + 1}</td>
                                <td style={pad}>{trans.text}</td>
                                <td style={pad}>{persons.filter(r => r.id == trans.paidBy)[0].name}</td>
                                <td style={pad}>{trans.paidTo.map(id => (
                                    persons.filter(r => r.id == id)[0].name + ", "
                                ))}</td>
                                <td style={pad}>{trans.amtDisplay}</td>
                            </tr>
                        </>
                    ))}
                </tbody>
            </table>
            <p style={fsty}>Person - wise list: -</p>
            <table className="table table-success table-striped">
                <thead>
                    <tr style={fsty}>
                        <th style={pad} scope="col">#</th>
                        <th style={pad} scope="col">Name</th>
                        <th style={pad} scope="col">Paid</th>
                        <th style={pad} scope="col">Expense</th>
                        <th style={pad} scope="col">Balance</th>
                    </tr>
                </thead>
                <tbody>
                    {personsDisplay.map((person, index) => (
                        <tr style={fsty} key={person.id}>
                            <th style={pad}>{index + 1}</th>
                            <th style={pad}>{person.name}</th>
                            <th style={pad}>{person.paid}</th>
                            <th style={pad}>{person.expense.toFixed(2)}</th>
                            <th style={pad}>{person.final.toFixed(2)}</th>
                        </tr>

                    ))}
                </tbody>
            </table>
            <p style={fsty}>Settlement table: -</p>
            <table className="table table-success table-striped">
                <thead>
                    <tr style={fsty}>
                        <th style={pad} scope="col">#</th>
                        <th style={pad} scope="col">From</th>
                        <th style={pad} scope="col">To</th>
                        <th style={pad} scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    {settlement1.map((person, index) => (
                        person.pay == 0 ? "" :
                            <tr style={fsty} key={index}>
                                <th style={pad}>{index + 1}</th>
                                <th style={pad}>{person.from}</th>
                                <th style={pad}>{person.to}</th>
                                <th style={pad}>{person.pay.toFixed(2)}</th>
                            </tr>
                    ))}
                </tbody>
            </table>
            
        </div>
    )
}
