import React, { useContext, useEffect, useState } from 'react'
import { GlobalContext } from './GlobalState'
import ReactDOMServer from "react-dom/server";
import jsPDF from "jspdf";
import { Invoice } from './Invoice';
const doc = new jsPDF();

export const Output = () => {
    const { transactions, persons, tripName } = useContext(GlobalContext);
    const save = () => {
        doc.html(ReactDOMServer.renderToStaticMarkup(<Invoice tripName={tripName} transactions={transactions1} persons={persons1} personsDisplay={personsDisplay} settlement1={settlement1} />), {
            callback: () => {
                var pageCount = doc.internal.getNumberOfPages();
                let i= pageCount-34
                for(i; i<pageCount; i++){
                    doc.deletePage(pageCount-33);
                }
                doc.save(tripName+"_invoice.pdf");
            }
        });
    };
    const settlement1 = [];
    
    const transactions1 = transactions;
    const persons1 = persons;
    const personsDisplay = [];
    for (let i = 0; i < transactions1.length; i++) {
        const amt = transactions1[i].amount / transactions1[i].paidTo.length;
        persons1.filter(r => r.id == transactions1[i].paidBy).map((row) => (row.paid = row.paid + transactions1[i].amount, transactions1[i].amount = 0));
        // console.log(transactions1[i].paidTo.length)
        let payArray = transactions1[i].paidTo;
        console.log(payArray)
        console.log(amt)
        for (let j = 0; j < transactions1[i].paidTo.length; j++) {
            persons1.filter(r => r.id == payArray[j]).map((row) => (row.expense = row.expense + amt))
        }
    }
    console.log("here")
    for (let k = 0; k < persons1.length; k++) {
        persons1[k].final = persons1[k].paid - persons1[k].expense;
        personsDisplay.push(persons1[k]);
    }
    console.log("here1")
    persons1?.sort((a, b) => (a.final > b.final ? -1 : 1))
    let i = 0;
    let j = persons1.length - 1;
    while (i < j) {
        if (persons1[i].final != 0 && persons1[j].final != 0) {
            if (Math.abs(persons1[i].final) > Math.abs(persons1[j].final)) {
                persons1[i].final += persons1[j].final;

                const newsettlement1 = {
                    from: persons1[j].name,
                    to: persons1[i].name,
                    pay: Math.abs(persons1[j].final)
                }
                settlement1.push(newsettlement1);
                persons1[j].final = 0;
                j = j - 1;
            }
            else {
                persons1[j].final += persons1[i].final;
                const newsettlement1 = {
                    from: persons1[j].name,
                    to: persons1[i].name,
                    pay: persons1[i].final
                }
                settlement1.push(newsettlement1);
                persons1[i].final = 0;
                i = i + 1;
            }
        }
        else if (persons1[i].final == 0 && persons1[j].final == 0) {
            i = i + 1;
            j = j - 1;
        }
        else if (persons1[i].final == 0) {
            i = i + 1;
        }
        else {
            j = j - 1
        }
    }
    console.log("here2")
    for (let k = 0; k < persons1.length; k++) {
        persons1[k].final = persons1[k].paid - persons1[k].expense;
    }
    console.log(persons1)
    console.log(transactions1)
    console.log(settlement1)
    // console.log(persons1)
    return (
        <div className='container'>
            Person-wise list: -
            <table className="table table-success table-striped">
                <thead>
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Name</th>
                        <th scope="col">Paid</th>
                        <th scope="col">Expense</th>
                        <th scope="col">Balance</th>
                    </tr>
                </thead>
                <tbody>
                    {personsDisplay.map((person, index) => (
                        <tr key={person.id}>
                            <th>{index + 1}</th>
                            <th>{person.name}</th>
                            <th>{person.paid}</th>
                            <th>{person.expense.toFixed(2)}</th>
                            <th>{person.final.toFixed(2)}</th>
                        </tr>

                    ))}
                </tbody>
            </table>
            <hr />
            Settlement table: -
            <table className="table table-success table-striped">
                <thead>
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">From</th>
                        <th scope="col">To</th>
                        <th scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    {settlement1.map((person, index) => (
                        person.pay == 0 ? "" :
                            <tr key={index}>
                                <th>{index + 1}</th>
                                <th>{person.from}</th>
                                <th>{person.to}</th>
                                <th>{person.pay.toFixed(2)}</th>
                            </tr>
                    ))}
                </tbody>
            </table>
            <button className="btn btn-next px-5" onClick={save}>Download Invoice</button>
        </div>
    )
}
