import '../App.css';
import React, { useState, useContext } from 'react'
import { GlobalContext } from './GlobalState';

export const AddPerson = () => {
    const [name, setname] = useState("");
    const { addPerson, persons } = useContext(GlobalContext)
    const onSubmit = e => {
        e.preventDefault()
        const newPerson = {
            id: persons.length + 1,
            name,
            expense: 0,
            paid: 0,
            final: 0
        }
        if (name === "") {
            alert("Please enter name")
        }
        else {
            addPerson(newPerson);
        }

        setname("")
    }

    return (
        <div>
            <form onSubmit={onSubmit}>
                <div className="form-control">
                    <div className='row'>

                        <label htmlFor="text"><b>Add new person</b></label>
                        <div className='col-7'>
                            <input type="text" placeholder="Enter your name..." value={name} onChange={(e) => setname(e.target.value)} />
                        </div>

                        <div className='col-5'>
                            <button className="btn btn-here">Add Person</button>
                        </div>
                        <p><b>Note: -</b> On Hover/Click on the name, a cross button will appear to delete the respective entry.</p>
                    </div>
                </div>
            </form>
        </div>
    )
}
