import React, { useContext } from 'react'
import { GlobalContext } from './GlobalState'
import { Transaction } from './Transaction'
export const FinalHistory = () => {
    const { transactions, persons } = useContext(GlobalContext)

    return (
        <div>
            <h3>History</h3>
            <table className='table table-bordered'>
                <thead>
                    <tr>
                    <th>#</th>
                    <th>Task</th>
                    <th>Paid by</th>
                    <th>Paid for</th>
                    <th>Amount </th>
                    </tr>
                </thead>
                <tbody>
                    {transactions.map((trans,index) => (
                        <>
                        <tr key={index}>
                            <td>{index+1}</td>
                            <td>{trans.text}</td>
                            <td>{persons.filter(r => r.id == trans.paidBy)[0].name}</td>
                            <td>{trans.paidTo.map(id => (
                                persons.filter(r => r.id == id)[0].name+", "
                            ))}</td>
                            <td>{trans.amount}</td>
                        </tr>
                        </>
                    ))}
                </tbody>
            </table>
        </div>
    )
}
